import React, { useState } from "react";

const FORM_ENDPOINT = "https://public.herotofu.com/v1/eeb6a380-9d64-11ec-bdf8-dd9c99f898ec";

function Contact() {

    const [ submitted, setSubmitted ] = useState(false);
    const handleSubmit = () => {
        setTimeout(() => {
            setSubmitted(true);
        }, 100);
    };

    if (submitted) {
        return (
            <div id='contact-form-submitted'>
                I'll be in touch soon.
            </div>
        );
    }

    return (
        <form action={FORM_ENDPOINT} onSubmit={handleSubmit} method="POST" target="_blank" className="contact-form" >
            <div>
                <input type="text" placeholder="Your name" name="name" required />
            </div>
            <div>
                <input type="email" placeholder="Email" name="email" required />
            </div>
            <div>
                <textarea placeholder="Your message" name="message" required />
            </div>
            <div>
                <button type="submit"> Send message </button>
            </div>
        </form>
    );
}

export default Contact;