import React, { Component } from 'react';
import './project-section.css';

class ProjectSection extends Component {

    render() {
        return (
            <div className='projectsections'>
                { this.props.sections && 
                    this.props.sections.map((section, index) => {
                        switch(section.type){
                            case 'image':
                                return null;
                            case 'link':
                                return null;
                            case 'list':
                                return null;
                            default: // add more cases for other media types
                                return this.renderBasicProjectSection(section, index);
                        }
                    })
                }
            </div>
        );
    }

    // function to handle images

    // function to handle figma links

    // function to handle any other external things

    // function to handle lists

    // generic section structure
    renderBasicProjectSection(section, index) {
        console.log(section);
        return (
            <div className={`projectsection`} key={index}>
                <h2 className={`projectsection-title`}>{section.title}</h2>
            </div>
        );
    }
}

export default ProjectSection;