import React, { Component } from 'react';
import './styles.css';
import DetailCard from '../detail-card/Detail-Card';
import ContactIcon from '../contact-icon/Contact-Icon';
import ResumeCard from '../resume-card/Resume-Card';
import { projectData } from '../../data';
import Contact from '../../pages/Contact';

class PageSection extends Component {

    constructor() {
        super();

        this.state = {
            option: "all"
        }

        this.chooseProjectDisplayType = this.chooseProjectDisplayType.bind(this);
    }

    render() {
        if (this.props.section === "top") {
            return this.renderTopSection();
        } else if (this.props.section === "about") {
            return this.renderAboutSection();
        } else if (this.props.section === "resume") {
            return this.renderResumeSection();
        } else if (this.props.section === "projects") {
            return this.renderProjectSection();
        } else {
            return null;
        }
    }

    renderTopSection() {
        return (
            <div id="top">
                <h1>sarah irons</h1>
            </div>
        );
    }

    renderAboutSection() {
        return (
            <div id="about">
                <div className="left">
                    <h3>send me a message</h3>
                    <Contact />
                </div>
                <div className="right">
                    <h3>or connect with me here:</h3>
                    <div className="icons" id="contact">
                        <ContactIcon contactUrl="https://www.linkedin.com/in/sarahlikesiron/" contactMethod="linkedin" contactInfo="sarahlikesiron" icon="Linkedin" contactDesc="Linkedin" />
                        <ContactIcon contactUrl="https://github.com/sarahlikesiron" contactMethod="github" contactInfo="sarahlikesiron" icon="Github" contactDesc="Github" />
                        <ContactIcon contactUrl="mailto:sli8695@rit.edu" contactMethod="email" contactInfo="sli8695(at)rit.edu" icon="Email" contactDesc="Email" />
                    </div>
                </div>
            </div>
        );
    }

    renderResumeSection() {
        return (

            <div id="resumes">
                <ResumeCard link="" image="resume_block.png" descText="(in progress)" />
                <ResumeCard link="./irons_resume_2-22.pdf" image="resume_block.png" descText="download resume" />
            </div>

        );
    }

    renderProjectSection() {
        return (
            // eventually will need to add a loop for all of the projects
            // add methods for choosing which option to view
            <div id="projectsSection">
                <h2>projects</h2>
                <ul id="ddpList">
                    <li id="allOption" className={`${this.state.option === "all" ? 'type-selected' : ''}`} onClick={() => this.chooseProjectDisplayType("all")}>all</li>
                    <li id="designOption" className={`${this.state.option === "design" ? 'type-selected' : ''}`} onClick={() => this.chooseProjectDisplayType("design")}>design</li>
                    <li id="developmentOption" className={`${this.state.option === "frontend" ? 'type-selected' : ''}`} onClick={() => this.chooseProjectDisplayType("frontend")}>frontend</li>
                    <li id="developmentOption" className={`${this.state.option === "backend" ? 'type-selected' : ''}`} onClick={() => this.chooseProjectDisplayType("backend")}>backend</li>
                    <li id="developmentOption" className={`${this.state.option === "mobile" ? 'type-selected' : ''}`} onClick={() => this.chooseProjectDisplayType("mobile")}>mobile</li>
                    <li id="developmentOption" className={`${this.state.option === "java" ? 'type-selected' : ''}`} onClick={() => this.chooseProjectDisplayType("java")}>java</li>
                    <li id="programmingOption" className={`${this.state.option === "other" ? 'type-selected' : ''}`} onClick={() => this.chooseProjectDisplayType("other")}>other</li>
                </ul>
                <div className='all'>
                    { this.shouldRenderAll() && projectData.map(project => {
                        return <DetailCard project={project} key={project.id} />
                    })}
                </div>
                <div className='design'>
                    { !this.shouldRenderAll() && projectData.map(project => {
                        if (project.types.includes(this.state.option)) {
                            return <DetailCard project={project} key={project.id} />
                        }
                    })}
                </div>
            </div>
        );
    }

    chooseProjectDisplayType(type) {
        this.setState({ option: type });
    }

    shouldRenderAll() {
        if (this.state.option === "all") {
            return true;
        }
        return false;
    }

    shouldRenderDesign() {
        if (this.state.option === "design") {
            return true;
        }
        return false;
    }

    shouldRenderDevelopment() {
        if (this.state.option === "development") {
            return true;
        }
        return false;
    }

    shouldRenderProgramming() {
        if (this.state.option === "programming") {
            return true;
        }
        return false;
    }

}

export default PageSection;