import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import './styles.css';

class DetailCard extends Component {

    render() {
        return (
            <div className="projLinkCard">
                {/* <Link to={`project/${(this.props.project.title).replaceAll(/ /gi, '_')}`}> */}
                <Link to={`/`}>
                    <div className="proj">
                        <img className="projImg" src={`./images/projectCard/${this.props.project.image}`} alt="temp"/>
                        <div className="desc">
                            <h2>{this.props.project.title}</h2>
                            <p>{this.props.project.attribute} - {this.props.project.semester}</p>
                        </div>
                    </div>
                </Link>
            </div>
        )
    }

}

export default DetailCard;