import React, { Component } from 'react';
import './contact-icon.css';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faLinkedin, faGithub } from '@fortawesome/free-brands-svg-icons';
import { faEnvelopeSquare } from '@fortawesome/free-solid-svg-icons';

class ContactIcon extends Component {

    render() {
        switch (this.props.icon) {
            case "Linkedin":
                return (
                    <div className="icon">
                        <a href={this.props.contactUrl}><FontAwesomeIcon icon={faLinkedin} size="2x" />{this.props.contactDesc}</a>
                        <span className="icontext">{this.props.contactInfo}</span>
                    </div>
                );
            case "Github":
                return (
                    <div className="icon">
                        <a href={this.props.contactUrl}><FontAwesomeIcon icon={faGithub} size="2x" />{this.props.contactDesc}</a>
                        <span className="icontext">{this.props.contactInfo}</span>
                    </div>
                );
            case "Email":
                return (
                    <div className="icon">
                        <a href={this.props.contactUrl}><FontAwesomeIcon icon={faEnvelopeSquare} size="2x" />{this.props.contactDesc}</a>
                        <span className="icontext">{this.props.contactInfo}</span>
                    </div>
                );
            default:
                return null;
        }
    }

}

export default ContactIcon;