import * as React from 'react';
import { useParams } from 'react-router-dom';
import { projectData } from '../data';
import ProjectHeader from '../components/project-header/Project-Header';
import ProjectSection from '../components/project-section/Project-Section';

let project = {};

function Project() {

    const { proj_id } = useParams();
    project = getProjectData(proj_id);

    if (project !== undefined) {
        return (
            <div>
                <ProjectHeader title={project.title} img={project.image} attribute={project.attribute}/>
                <div>
                    <ProjectSection sections={project.sections} />
                </div>
            </div>
        )
    }

    return (
        <div>
            <h2>Project not found.</h2>
        </div>
    );
}

function getProjectData(proj_id) {
    return projectData.find(project => project.id === proj_id);
}

export default Project;